<template>
  <div id="reapplication">
    <transition name="funcList">
      <div v-if="applicationStatus" class="func-operation">
        <div v-for="(item, index) in reimbursement" v-bind:key="index" class="money-item" :class="{'error': item.error, 'active': inputIndex === index}" @click="setValue(index)">
          <p class="left">{{item.name}}</p>
          <input :type="item.type" :placeholder="renderPlace(index)" @input="inputValue(index)" @blur="showValue(index)">
        </div>
        <div id="submit" class="pointer" @click="submit">提交</div>
      </div>
    </transition>
    <transition name="modal">
      <DatePicker v-if="inputIndex === 0" :date="date" @hide="cancelInput" @pick="inputDate"></DatePicker>
    </transition>
    <transition name="modal">
      <div v-if="inputIndex === 1" class="picker" :style="this.$root.modalHeight(1)">
        <p>请选择类型</p>
        <div class="type-wrapper">
          <div class="type pointer" @touchstart="setType" @touchmove="setType" @touchend="setType">
            <div v-for="(item, index) in renderType" v-bind:key="index" @click="setType2(index)">
              <p>{{item}}</p>
            </div>
            <div class="center-line">
            </div>
          </div>
        </div>
        <div class="choices">
          <div class="modal-cancel" @click="cancelInput">取消</div>
          <div class="modal-submit" @click="input">确定</div>
        </div>
      </div>
    </transition>
  </div>
</template>
<!--#757575-->
<script>
import $ from 'jquery'
import DatePicker from './DatePicker'
export default {
  name: 're-application',
  data () {
    return {
      applicationStatus: false,
      reimbursement: [
        {name: '起止日期', id: 'expense_date', type: 'text', error: false},
        {name: '费用类型', id: 'expense_type', type: 'text', error: false},
        {name: '费用描述', id: 'expense_description', type: 'text', error: false},
        {name: '发票索引', id: 'receipt_reference', type: 'text', error: false},
        {name: '发票金额', id: 'receipt_amount', type: 'text', error: false}
      ],
      inputIndex: -1,
      date: {
        year: 0,
        month: 0,
        day: 0
      },
      typeList: [
        '市内交通费',
        '在途费用',
        '差旅补贴'
      ],
      typeIndex: -1,
      tempTypeIndex: -1,
      number: 0
    }
  },
  methods: {
    application: function () {
      $.ajax({
        async: false,
        url: '/expense_application/',
        type: 'POST',
        data: {expense_date: $('#expense_date').val(),
          expense_description: $('#expense_description').val(),
          expense_type: $('#expense_type').val(),
          receipt_reference: $('#receipt_reference').val(),
          receipt_currency: 'CNY',
          receipt_amount: $('#receipt_amount').val(),
          exchange_rate: 1.0000,
          converted_amount: $('#converted_amount').val(),
          // eslint-disable-next-line no-undef
          csrfmiddlewaretoken: csrf_token},
        success: function (data) {
          alert(data)
        }
      })
    },
    setValue: function (i) {
      this.inputIndex = i
      if (i > 1) {
        $('.money-item input').eq(i).focus()
      }
      var height
      switch (i) {
        case 0:
          $('.money-item input').eq(i).blur()
          this.$root.eventHub.$emit('prompt')
          break
        case 1:
          $('.money-item input').eq(i).blur()
          if ($('.money-item input').eq(1).val() === '') {
            this.typeIndex = 1
          }
          this.tempTypeIndex = this.typeIndex
          this.$root.eventHub.$emit('prompt')
          break
        case 2:
          if (!this.$root.adapt) {
            height = $('#main').height() + 58 * 3
            $('#app').animate({scrollTop: height}, 400)
          }
          break
        case 3:
          if (!this.$root.adapt) {
            height = $('#main').height() + 58 * 4
            $('#app').animate({scrollTop: height}, 400)
          }
          break
        case 4:
          if (!this.$root.adapt) {
            height = $('#main').height() + 58 * 5
            $('#app').animate({scrollTop: height}, 400)
          }
          if (event.target.value !== '') {
            $('.money-item input').eq(4).val(this.number)
          }
          break
      }
    },
    setType: function (e) {
      e.preventDefault()
      e.stopPropagation()
      if (!e) {
        e = window.event
      }
      switch (e.type) {
        case 'touchstart':
          this.posY = e.targetTouches[0].pageY
          break
        case 'touchmove':
          var temp = e.targetTouches[0].pageY - this.posY
          if (temp > 40) {
            this.posY = e.targetTouches[0].pageY
            if (this.tempTypeIndex > 0) {
              this.tempTypeIndex--
            } else {
              this.tempTypeIndex = 0
            }
          } else if (temp < -40) {
            this.posY = e.targetTouches[0].pageY
            if (this.tempTypeIndex < 2) {
              this.tempTypeIndex++
            } else {
              this.tempTypeIndex = 2
            }
          }
          break
        case 'touchend':
          break
      }
    },
    setType2: function (i) {
      if (this.$root.adapt) {
        switch (i) {
          case 0:
            if (this.tempTypeIndex > 0) {
              this.tempTypeIndex--
            }
            break
          case 2:
            if (this.tempTypeIndex < 2) {
              this.tempTypeIndex++
            }
            break
        }
      }
    },
    inputValue: function (i) {
      switch (i) {
        case 4:
          this.number = event.target.value
          break
      }
    },
    showValue: function (i) {
      if (event.target.value !== '') {
        this.reimbursement[i].error = false
      }
      switch (i) {
        case 2:
        case 3:
          this.inputIndex = -1
          break
        case 4:
          this.inputIndex = -1
          if (event.target.value === '') {
            this.number = 0
          }
          event.target.value = this.number + ' 元'
          break
      }
    },
    input: function () {
      this.reimbursement[this.inputIndex].error = false
      this.typeIndex = this.tempTypeIndex
      $('.money-item input').eq(1).val(this.typeList[this.typeIndex])
      this.cancelInput()
    },
    inputDate: function (data) {
      this.date = data
      $('.money-item input').eq(0).val(this.date.year + '年' + this.date.month + '月' + this.date.day + '日')
      this.cancelInput()
    },
    cancelInput: function () {
      this.$root.eventHub.$emit('prompt')
      this.hideModal()
    },
    hideModal: function () {
      this.inputIndex = -1
    },
    renderPlace: function (i) {
      switch (i) {
        case 0:
          return '请输入活动日期'
        case 1:
          return '请选择费用类型'
        case 2:
          return '必填'
        case 3:
          return '必填'
        case 4:
          return '例: 30元'
      }
    },
    submit: function () {
      var tag = true
      var input = $('.money-item input')
      for (var i = 0; i < input.length; i++) {
        if (input[i].value === '') {
          this.reimbursement[i].error = true
          tag = false
        }
      }
      if (tag) {
        var date = this.date.year + '.' + this.date.month + '.' + this.date.day
        var amount = (this.number * 1.0000).toFixed(2)
        var self = this
        $.ajax({
          async: false,
          url: '/expense_application/',
          type: 'POST',
          data: {
            expense_date: date,
            expense_description: $('.money-item input').eq(2).val(),
            expense_type: $('.money-item input').eq(1).val(),
            receipt_reference: $('.money-item input').eq(3).val(),
            receipt_currency: 'CNY',
            receipt_amount: amount,
            exchange_rate: 1.0000,
            converted_amount: amount,
            // eslint-disable-next-line no-undef
            csrfmiddlewaretoken: csrf_token
          },
          success: function (data) {
            var mode
            if (data === 'success') {
              mode = {
                text: '成功提交报销申请！',
                duration: 1000
              }
              self.$root.eventHub.$emit('showPrompt', mode)
              self.$router.push({path: '/user/reimbursement'})
            } else {
              mode = {
                text: '提交失败！',
                duration: 1000
              }
              self.$root.eventHub.$emit('showPrompt', mode)
            }
          }
        })
      } else {
        var mode = {
          text: '请输入完整的报销信息！',
          duration: 1200
        }
        this.$root.eventHub.$emit('showPrompt', mode)
      }
    }
  },
  computed: {
    renderType: function () {
      var type = []
      var i
      switch (this.tempTypeIndex) {
        case 0:
          type.push('')
          type.push(this.typeList[0])
          type.push(this.typeList[1])
          break
        case 1:
          for (i = 0; i < 3; i++) {
            type.push(this.typeList[i])
          }
          break
        case 2:
          type.push(this.typeList[1])
          type.push(this.typeList[2])
          type.push('')
          break
      }
      return type
    }
  },
  components: {
    DatePicker
  },
  mounted () {
    var self = this
    this.applicationStatus = true
    this.$root.getCurrentDate(this.date)
    this.$root.eventHub.$on('cancelPrompt', function () {
      if (self.inputIndex !== -1) {
        self.hideModal()
      }
    })
  }
}
</script>

<style scoped>
  .func-operation{
    height: auto;
    position: relative;
    z-index: 3;
  }
  .funcList-enter-active, .operation-enter-active{
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
  }
  .funcList-enter, .operation-enter{
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    -moz-transform: translate3d(0, -100%, 0);
    -ms-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  .money-item{
    width: 100%;
    border-bottom: 1px solid #f0f2f5;
    overflow: hidden;
    background-color: white;
    position: relative;
    z-index: 3;
  }
  .money-item.active{
    border-bottom: 2px solid #3cb371;
  }
  .money-item.error{
    border-bottom: 2px solid #dc143c;
  }
  .money-item p{
    border: 0;
    outline: none;
  }
  .money-item input{
    float: right;
    padding: 0;
    border: 0;
    outline: 0;
    text-align: right;
  }
  .type-wrapper{
    width: calc(100% - 60px);
    height: auto;
    overflow: hidden;
    padding: 5px 30px 15px;
  }
  .type{
    width: 100%;
    height: 120px;
    position: relative;
    user-select: none;
  }
  .type>div{
    height: 40px;
    text-align: center;
    user-select: none;
  }
  .type p{
    line-height: 40px;
  }
  .center-line{
    width: 100%;
    border-top: 1px solid #D3D3D3;
    border-bottom: 1px solid #D3D3D3;
    position: absolute;
    top: 40px;
  }
  .choices{
    clear: left;
    overflow: hidden;
  }
  .modal-cancel, .modal-submit{
    width: 120px;
    height: 40px;
    margin-top: 10px;
    float: left;
    border-radius: 3px;
    color: white;
    line-height: 40px;
    text-align: center;
    user-select: none;
    cursor: pointer;
  }
  .modal-cancel{
    margin-left: calc((100% - 270px) / 2);
    background: #dc143c;
  }
  .modal-submit{
    margin-left: 30px;
    background: #3cb371;
  }
  #submit{
    text-align: center;
    color: white;
    margin-bottom: 50px;
    background: #3cb371;
    border-radius: 3px;
    user-select: none;
    -webkit-transition: background-color ease-out .2s;
    -moz-transition: background-color ease-out .2s;
    -o-transition: background-color ease-out .2s;
    transition: background-color ease-out .2s;
  }
  #submit:active{
    background: #2EA057;
  }
  .picker{
    background: white;
    position: fixed;
    z-index: 30;
  }
  .picker>p:first-of-type{
    line-height: 40px;
    padding: 5px;
    text-align: center;
    font-size: 17px;
  }
  .type p, .modal-cancel, .modal-submit{
    font-size: 17px;
  }

  @media only screen and (max-device-width: 767px) {
    .func-operation {
      width: calc(100% - 40px);
      margin: 0 20px;
    }
    .money-item p{
      font-size: 17px;
      height: 41px;
      line-height: 41px;
      margin: 8px 0;
    }
    .money-item input{
      font-size: 17px;
      height: 41px;
      line-height: 41px;
      margin: 8px 0;
      width: calc(100% - 80px);
    }
    .money-item.error p, .money-item.error input, .money-item.active p, .money-item.active input{
      margin: 8px 0 7px;
    }
    .money-item:first-of-type input, .money-item:nth-of-type(2) input{
      color: transparent;
      text-shadow: 0 0 0 #000000;
    }
    .money-item:nth-of-type(3) input::-moz-selection, .money-item:nth-of-type(5) input::-moz-selection{
      color: white;
      background-color: #3cb371;
    }
    .money-item:nth-of-type(3) input::selection, .money-item:nth-of-type(5) input::selection{
      color: white;
      background-color: #3cb371;
    }
    #submit{
      width: 100%;
      height: 42px;
      line-height: 42px;
      margin: 20px 0 50px;
      font-size: 17px;
    }
    .picker{
      width: 100%;
      bottom: 0;
      left: 0;
    }
  }

  @media only screen and (min-device-width: 768px) {
    .func-operation {
      width: calc(100% - 36px);
      margin: 0 18px;
    }
    .money-item{
      width: calc(100% - 40px);
      padding: 10px 20px;
    }
    .money-item p{
      font-size: 18px;
      height: 39px;
      line-height: 39px;
    }
    .money-item input{
      font-size: 18px;
      height: 39px;
      line-height: 39px;
      width: calc(100% - 100px);
    }
    .money-item.error p, .money-item input, .money-item.active p, .money-item.active input{
      height: 38px;
    }
    #submit{
      width: 200px;
      height: 42px;
      margin: 30px calc((100% - 200px) / 2) 10px;
      font-size: 18px;
      line-height: 42px;
    }
    .picker{
      width: 396px;
      height: 300px;
      left: calc((100% - 396px) / 2);
      top: calc((100% - 300px) / 2);
    }
  }
</style>



