<template xmlns:v-finger="http://www.w3.org/1999/xhtml">
  <div class="picker" :style="this.$root.modalHeight(0)">
    <p>请选择时间</p>
    <div class="date-wrapper">
      <div class="year date pointer" v-finger:press-move="setYear">
        <div v-for="(i, index) in renderYear" v-bind:key="index" @click="setYear2(index)">
          <p>{{i}}&nbsp;年</p>
        </div>
        <div class="center-line">
        </div>
      </div>
      <div class="month date pointer" v-finger:press-move="setMonth">
        <div v-for="(i, index) in renderMonth" v-bind:key="index" @click="setMonth2(index)">
          <p>{{i}}&nbsp;月</p>
        </div>
        <div class="center-line">
        </div>
      </div>
      <div class="day date pointer" v-finger:press-move="setDay">
        <div v-for="(i, index) in renderDay" v-bind:key="index" @click="setDay2(index)">
          <p>{{i}}&nbsp;日</p>
        </div>
        <div class="center-line">
        </div>
      </div>
    </div>
    <div class="choices">
      <div class="modal-cancel" @click="cancelInput">取消</div>
      <div class="modal-submit" @click="input">确定</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'date-picker',
  data () {
    return {
      tempYear: this.date.year,
      tempMonth: this.date.month,
      tempDay: this.date.day,
      posY: 0
    }
  },
  props: {
    date: {
      type: Object,
      default: function () {
        return {
          year: 1970,
          month: 1,
          day: 1
        }
      }
    }
  },
  methods: {
    setYear: function (ev) {
      ev.preventDefault()
      ev.stopPropagation()
      this.tempYear -= ev.deltaY * 0.06
      var y = Math.round(this.tempYear)
      var m = Math.round(this.tempMonth)
      var d = Math.round(this.tempDay)
      if ((y % 4 === 0 && y % 100 !== 0) || y % 400 === 0) {
        if (m === 2 && d === 29) {
          this.tempDay = 28
        }
      }
    },
    setYear2: function (i) {
      if (this.$root.adapt) {
        switch (i) {
          case 0:
            if ((this.tempYear % 4 === 0 && this.tempYear % 100 !== 0) || this.tempYear % 400 === 0) {
              if (this.tempMonth === 2 && this.tempDay === 29) {
                this.tempDay = 28
              }
            }
            this.tempYear--
            break
          case 2:
            if ((this.tempYear % 4 === 0 && this.tempYear % 100 !== 0) || this.tempYear % 400 === 0) {
              if (this.tempMonth === 2 && this.tempDay === 29) {
                this.tempDay = 28
              }
            }
            this.tempYear++
            break
        }
      }
    },
    setMonth: function (ev) {
      ev.preventDefault()
      ev.stopPropagation()
      this.tempMonth -= ev.deltaY * 0.06
      var y = Math.round(this.tempYear)
      var m = Math.round(this.tempMonth)
      var d = Math.round(this.tempDay)
      var temp = this.getDaysInMonth(y, m)
      if (d > temp) {
        this.tempDay = temp
      }
      if (m < 1) {
        this.tempYear--
        this.tempMonth = 12
      } else if (m > 12) {
        if (d === this.getDaysInMonth(y, 12)) {
          this.tempDay = 1
        }
        this.tempYear++
        this.tempMonth = 1
      }
    },
    setMonth2: function (i) {
      if (this.$root.adapt) {
        switch (i) {
          case 0:
            this.tempMonth--
            if (this.tempDay > this.getDaysInMonth(this.year, this.tempMonth)) {
              this.tempDay = this.getDaysInMonth(this.year, this.tempMonth)
            }
            if (this.tempMonth === 0) {
              this.tempYear--
              this.tempMonth = 12
            }
            break
          case 2:
            this.tempMonth++
            if (this.tempDay > this.getDaysInMonth(this.year, this.tempMonth)) {
              this.tempDay = this.getDaysInMonth(this.year, this.tempMonth)
            }
            if (this.tempMonth === 13) {
              if (this.tempDay === this.getDaysInMonth(this.year, 12)) {
                this.tempDay = 1
              }
              this.tempYear++
              this.tempMonth = 1
            }
            break
        }
      }
    },
    setDay: function (ev) {
      ev.preventDefault()
      ev.stopPropagation()
      this.tempDay -= ev.deltaY * 0.06
      var y = Math.round(this.tempYear)
      var m = Math.round(this.tempMonth)
      var d = Math.round(this.tempDay)
      if (d < 1) {
        if (m === 1) {
          this.tempYear--
          this.tempMonth = 12
        } else {
          this.tempMonth--
        }
        this.tempDay = this.getDaysInMonth(Math.round(this.tempYear), Math.round(this.tempMonth))
      } else if (d > this.getDaysInMonth(y, m)) {
        this.tempDay = 1
        if (m === 12) {
          this.tempYear++
          this.tempMonth = 1
        } else {
          this.tempMonth++
        }
      }
    },
    setDay2: function (i) {
      if (this.$root.adapt) {
        switch (i) {
          case 0:
            this.tempDay--
            if (this.tempDay === 0) {
              if (this.tempMonth === 1) {
                this.tempYear--
                this.tempMonth = 12
              } else {
                this.tempMonth--
              }
              this.tempDay = this.getDaysInMonth(this.tempYear, this.tempMonth)
            }
            break
          case 2:
            this.tempDay++
            if (this.tempDay > this.getDaysInMonth(this.tempYear, this.tempMonth)) {
              this.tempDay = 1
              if (this.tempMonth === 12) {
                this.tempYear++
                this.tempMonth = 1
              } else {
                this.tempMonth++
              }
            }
            break
        }
      }
    },
    getDaysInMonth: function (year, month) {
      var temp = new Date(year, month, 0)
      return temp.getDate()
    },
    input: function () {
      var x = {
        year: Math.round(this.tempYear),
        month: Math.round(this.tempMonth),
        day: Math.round(this.tempDay)
      }
      this.$emit('pick', x)
    },
    cancelInput: function () {
      this.$emit('hide')
    }
  },
  computed: {
    renderYear: function () {
      var year = []
      var y = Math.round(this.tempYear)
      for (var i = y - 1; i <= y + 1; i++) {
        year.push(i)
      }
      return year
    },
    renderMonth: function () {
      var month = []
      var m = Math.round(this.tempMonth)
      for (var i = m - 1; i <= m + 1; i++) {
        if (i < 1) {
          month.push(12)
        } else if (i > 12) {
          month.push(1)
        } else {
          month.push(i)
        }
      }
      return month
    },
    renderDay: function () {
      var day = []
      var y = Math.round(this.tempYear)
      var m = Math.round(this.tempMonth)
      var d = Math.round(this.tempDay)
      for (var i = d - 1; i <= d + 1; i++) {
        if (i < 1) {
          if (m === 1) {
            day.push(this.getDaysInMonth(y - 1, 12))
          } else {
            day.push(this.getDaysInMonth(y, m - 1))
          }
        } else if (i > this.getDaysInMonth(y, m)) {
          day.push(1)
        } else {
          day.push(i)
        }
      }
      return day
    }
  }
}
</script>

<style scoped>
  .picker{
    background: white;
    position: fixed;
    z-index: 30;
  }
  .picker>p:first-of-type{
    line-height: 40px;
    padding: 5px;
    text-align: center;
    font-size: 17px;
  }
  .date-wrapper{
    width: calc(100% - 60px);
    height: auto;
    overflow: hidden;
    padding: 5px 30px 15px;
  }
  .date{
    height: 120px;
    overflow: hidden;
    float: left;
    position: relative;
  }
  .date>div{
    height: 40px;
    text-align: center;
    user-select: none;
  }
  .date p{
    line-height: 40px;
  }
  .year{
    width: 40%;
  }
  .month, .day{
    width: 30%;
  }
  .date p, .modal-cancel, .modal-submit{
    font-size: 17px;
  }
  .center-line{
    width: 100%;
    border-top: 1px solid #D3D3D3;
    border-bottom: 1px solid #D3D3D3;
    position: absolute;
    top: 40px;
  }
  .choices{
    clear: left;
    overflow: hidden;
  }
  .modal-cancel, .modal-submit{
    width: 120px;
    height: 40px;
    margin-top: 10px;
    float: left;
    border-radius: 3px;
    color: white;
    line-height: 40px;
    text-align: center;
    user-select: none;
    cursor: pointer;
  }
  .modal-cancel{
    margin-left: calc((100% - 270px) / 2);
    background: #dc143c;
  }
  .modal-submit{
    margin-left: 30px;
    background: #3cb371;
  }

  @media only screen and (max-device-width: 767px) {
    .picker{
      width: 100%;
      bottom: 0;
      left: 0;
    }
  }

  @media only screen and (min-device-width: 768px) {
    .picker{
      width: 396px;
      height: 300px;
      left: calc((100% - 396px) / 2);
      top: calc((100% - 300px) / 2);
    }
  }
</style>



